.sliderContainer {
  position: relative;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.sliderCard {
  display: flex !important;
  flex-direction: column;
  align-items: center !important;
  width: 100%;
  max-width: 200px;
  padding: 20px;
  transition: transform 0.3s ease;
  flex-shrink: 1 !important;
}

.buttonsBlock {
  position: absolute;
  top: 34%;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.sliderButton {
  &Prev {
    z-index: 10;
    left: -20px;
    width: 48px;
    height: 48px;
    background-color: #fff;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    display: flex;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transform: rotate(180deg);
  }
  &Next {
    z-index: 10;
    width: 48px;
    height: 48px;
    background-color: #fff;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    display: flex;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    right: -120px;
  }
}
