.container {
  position: relative;
  width: 100%;
}
.inputContainer {
  width: 100%;
  box-sizing: border-box;
}

.label {
  display: flex;
  align-self: flex-start;
  top: -20px;
  left: 0;
  font-weight: 400;
  font-size: 16px;
  color: #989898;
}

.errorContainer {
  display: flex;
  width: 100%;
  height: 20px;
}

.input {
  width: calc(100% - 25px);
  padding: 10px;
  font-weight: 400;
  font-size: 16px;
  color: #989898;
  border: 1px solid #247e6f;
  border-radius: 3px;
}

.input:focus {
  outline: none;
  box-shadow: none;
}

.inputError {
  display: flex;
  align-self: flex-start;
  margin-bottom: 5px;
  width: calc(100% - 25px);
  padding: 10px;

  font-weight: 400;
  font-size: 16px;
  color: #989898;
  border: 1px solid #247e6f;
  border-radius: 3px;
}

.input[type="number"]::-webkit-outer-spin-button,
.input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.error {
  font-weight: 500;
  top: 62px;
  // position: absolute;
  font-size: 18px;
  color: #e2574b;
}
