.button {
  background-color: inherit;
  border: 1px solid #79bf94;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 13px;
  padding: 16px 35px;
  font-weight: 500;
  font-size: 20px;
  color: #79bf94;
  transition: 0.2s opacity ease-in-out;
  margin: 0 auto;

  &:hover {
    cursor: pointer;
    opacity: 0.7;
    transition: 0.2s opacity ease-in-out;
  }
}
