.header {
  display: flex;
  align-items: center;
  gap: 32px;
}
.personDetails {
  padding-top: 40px;
  text-align: left;
  text-decoration: none;
}

.name {
  font-size: 30px;
}

.closeButton {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: inherit;
  border: none;
  cursor: pointer;
}

.loaderContainer {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100%;
}
