.imageContainer {
  height: 144px;
  width: 144px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;

  &:hover {
    transition: ease-in-out 0.2s;
    transform: scale(1.02);
  }
}

.imageContainerBig {
  height: 200px;
  width: 200px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.imageNone {
  height: 144px;
  width: 144px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-color: gray;
  cursor: pointer;
  opacity: 0.9;
}

.previewImage {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}
