.cardWrapper {
  width: 340px;
  height: 180px;
  background-color: #1a1a1a;
  color: white;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  font-family: "Arial", sans-serif;
}

.cardHolder,
.cardNumber,
.cardExpiry,
.cardCvv {
  display: flex;
  flex-direction: column;
}

.cardHolder span,
.cardNumber span,
.cardExpiry span,
.cardCvv span {
  font-size: 12px;
  color: #ccc;
}

.cardNumber {
  font-size: 20px;
  letter-spacing: 2px;
  margin: 10px 0;
}

.cardHolder {
  text-transform: uppercase;
}

.cardExpiry {
  display: flex;
  justify-content: space-between;
  width: 100px;
}

.cardExpiry div {
  gap: 10px;
  text-align: center;
}

.cardCvv {
  align-self: flex-end;
  text-align: right;
}
