.modalRoot {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 2001;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-animation-name: fadeIn;
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;
  padding: 30px;
  box-sizing: border-box;
}

.modalBackdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  backdrop-filter: blur(2px);
}

.modalContent {
  position: relative;
  z-index: 3;
  background-color: #f5f5f5;
  padding: 60px;
  border-radius: 10px;
  width: 100%;
  max-width: 890px;
  height: 100%;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  height: 100%;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}

.closeButton {
  position: fixed;
  top: 7%;
  right: 5%;
  background-color: inherit;
  border: none;
  cursor: pointer;
}

.modalBody {
  max-height: 100%;
  height: 100%;
  position: relative;
}

@-webkit-keyframes slideIn {
  from {
    bottom: -300px;
  }
  to {
    bottom: 0;
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    bottom: -300px;
  }
  to {
    bottom: 0;
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  from {
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
  }
  to {
    opacity: 1;
  }
}
